@import '/src/styles/styles.scss';
.button {
  display: none;
  @include media-min(tablet) {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    // padding: 8px 20px;
    gap: 8px;
    background-color: getColor('full-blue');
    outline: transparent;
    border-radius: 40px;
    color: getColor('full-white');
    width: 129px;
    height: 40px;
    border: 2px solid getColor('full-blue');
  }
}
.icon {
  fill: getColor('full-white');
  width: 24px;
  height: 24px;
}
